@charset "UTF-8";
/** Media Queries **/
.wc_decripciones {
  margin-top: 3rem;
}
.wc_decripciones h1 {
  font-size: 2.2rem;
  font-weight: 400;
  margin: 0 0 1.5rem 0;
}
.wc_decripciones h1 span {
  font-weight: 900;
}
@media (min-width: 750px) {
  .wc_decripciones h1 {
    font-size: 2.5rem;
  }
}
.wc_decripciones ul li {
  color: #000000;
}
.wc_decripciones p {
  color: #000000;
  text-align: justify;
  line-height: 1.5;
}
.wc_decripciones p:last-of-type {
  margin: 0;
}

.accesos {
  display: flex;
  gap: 0.5rem;
}
.accesos a {
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  width: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.accesos .intranet {
  background-color: #1D57C5;
  color: #FFFFFF;
  gap: 0.5rem;
}
.accesos .intranet:hover {
  background-color: rgb(15.9115044248, 47.7345132743, 108.0884955752);
}

