  align-items: center;
  justify-content: center;
}
.seccion-footer__redes a:hover {
  color: rgb(229.5, 229.5, 229.5);
  transform: scale(1.1);
  cursor: pointer;
}
.seccion-footer__redes a span {
  display: none;
}

